'use client';
import { Button } from '@/components/ui/button';
import TerritoryMap from '@/components/territoryMap';
import { useRouter } from 'next/navigation';
import { useState } from 'react';
import { useEffect } from 'react';
import { createClient } from '@/lib/supabase/client';
import { StatusColors, LeadStatus } from '@/types/Lead';

export default function Tabs() {
    const router = useRouter();
    const [activeTab, setActiveTab] = useState('tab1');
    const [leadsSubTab, setLeadsSubTab] = useState('view');
    const tabs = [
        { id: 'canvassing', label: 'Door Knocking' },
        { id: 'leads', label: 'Leads List' },
        { id: 'inspections', label: 'Inspections' },
    ];

    function LeadsContent() {
    const [leadsTab, setLeadsTab] = useState('view');
    const [leads, setLeads] = useState<any[]>([]);
    const [searchQuery, setSearchQuery] = useState('');
    const [loading, setLoading] = useState(false);
    const router = useRouter();

    // Fetch leads when component mounts
    useEffect(() => {
        fetchLeads();
    }, []);

    const fetchLeads = async () => {
        setLoading(true);
        try {
            const supabase = createClient();
            const { data: { user } } = await supabase.auth.getUser();
            
            if (!user) return;

            const { data: profile } = await supabase
                .from('profiles')
                .select('company_id')
                .eq('id', user.id)
                .single();

            const { data: leadsData, error } = await supabase
                .from('leads')
                .select('*')
                .eq('company_id', profile?.company_id)
                .order('date_added', { ascending: false });

            if (error) throw error;
            setLeads(leadsData || []);
        } catch (err) {
            console.error('Error fetching leads:', err);
        } finally {
            setLoading(false);
        }
    };

    // Filter leads based on search
    const filteredLeads = leads.filter(lead => {
        const search = searchQuery.toLowerCase();
        return (
            lead.full_name.toLowerCase().includes(search) ||
            lead.email.toLowerCase().includes(search) ||
            lead.address.toLowerCase().includes(search) ||
            lead.status.toLowerCase().includes(search)
        );
    });

    function LeadMiniCard({ lead, onClick }: { lead: any; onClick: () => void }) {
    const statusColor = StatusColors[lead.status as LeadStatus];
    
    return (
        <div
            onClick={onClick}
            className="border rounded-lg p-4 hover:shadow-lg transition-shadow cursor-pointer bg-white"
        >
            <div className="flex justify-between items-start mb-2">
                <h3 className="font-bold text-lg">{lead.full_name}</h3>
                <span
                    className="px-3 py-1 rounded-full text-xs font-semibold text-white"
                    style={{ backgroundColor: statusColor }}
                >
                    {lead.status}
                </span>
            </div>
            
            <div className="space-y-1 text-sm text-gray-600">
                <p>üìß {lead.email}</p>
                <p>üìû {lead.phone_number}</p>
                <p>üìç {lead.address}</p>
                <div className="flex justify-between mt-2 text-xs text-gray-500">
                    <span>Added: {new Date(lead.date_added).toLocaleDateString()}</span>
                    {lead.date_last_contacted && (
                        <span>Last Contact: {new Date(lead.date_last_contacted).toLocaleDateString()}</span>
                    )}
                </div>
            </div>
        </div>
    );
}

    return (
        <div>
            {/* Sub-tabs */}
            <div className="flex gap-2 mb-4">
                <button
                    onClick={() => setLeadsTab('view')}
                    className={`px-4 py-2 rounded ${
                        leadsTab === 'view'
                            ? 'bg-indigo-900 text-white'
                            : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
                    }`}
                >
                    View Leads
                </button>
                <button
                    onClick={() => setLeadsTab('add')}
                    className={`px-4 py-2 rounded ${
                        leadsTab === 'add'
                            ? 'bg-indigo-900 text-white'
                            : 'bg-gray-200 text-gray-700 hover:bg-gray-300'
                    }`}
                >
                    Add New Lead
                </button>
            </div>

            {/* View Leads Tab */}
            {leadsTab === 'view' && (
                <div>
                    {/* Search Bar */}
                    <input
                        type="text"
                        placeholder="Search by name, email, address, or status..."
                        value={searchQuery}
                        onChange={(e) => setSearchQuery(e.target.value)}
                        className="w-full p-3 border rounded-lg mb-4"
                    />

                    {/* Leads List */}
                    {loading ? (
                        <p className="text-center py-8">Loading leads...</p>
                    ) : filteredLeads.length === 0 ? (
                        <p className="text-center py-8 text-gray-500">
                            {searchQuery ? 'No leads match your search' : 'No leads yet. Drop some pins!'}
                        </p>
                    ) : (
                        <div className="max-h-[500px] overflow-y-auto space-y-3">
                            {filteredLeads.map((lead) => (
                                <LeadMiniCard
                                    key={lead.id}
                                    lead={lead}
                                    onClick={() => router.push(`/dashboard/leads/${lead.id}`)}
                                />
                            ))}
                        </div>
                    )}
                </div>
            )}

            {/* Add New Lead Tab */}
            {leadsTab === 'add' && (
                <div>
                    <p className="text-gray-600 mb-4">Manual lead creation form will go here</p>
                    {/* We'll build this next */}
                </div>
            )}
        </div>
    );
}

    return (
        <div className="w-full max-w-4xl mx-auto mt-8">
            {/* Tabs Navigation */}
            {tabs.map((tab) => (
                <button
                    key={tab.id}
                    onClick={() => setActiveTab(tab.id)}
                    className={`px-4 py-2 mr-2 rounded-t
                         ${activeTab === tab.id ? 'bg-blue-500 text-white' : 'bg-gray-200 hover:bg-gray-300 text-gray-700'}
                         `}
                >
                    {tab.label}
                </button>
            ))}
            {/* Tabs Content */}
            <div className="border border-gray-200 p-4 bg-white">
                {activeTab === 'canvassing' && (
                    <div>
                        <h2 className="text-xl font-bold mb-4"><Button variant="outline" onClick={() => router.push('/territoryManagement')}>Door Knocking / Territory Management</Button></h2>
                        <p className="text-xl font-bold mb-4">View your dropped pins here or press the button above to Log</p>
                        <TerritoryMap />
                    </div>
                )}
                {activeTab === 'leads' && (
                    <div>
                        <h2 className="text-xl font-bold mb-4">Leads List</h2>
                        <button 
                            onClick={() => setLeadsSubTab('view')}
                            className={leadsSubTab === 'view' ? 'active-styles' : 'inactive-styles'}
                        >
                            View Leads
                        </button><br></br>
                        <button 
                            onClick={() => setLeadsSubTab('add')}
                            className={leadsSubTab === 'add' ? 'active-styles' : 'inactive-styles'}
                        >
                            Add New Lead
                        </button>
                    </div>
                )}
                {activeTab === 'inspections' && (
                    <div>
                        <h2 className="text-xl font-bold mb-4">Inspections</h2>
                        <p>Calendar with a list of completed inspections and scheduled inspections.</p>
                    </div>
                )}
            </div>
        </div>
    );
} 